.form__input {
  margin: 0;
  padding: 0 0 9px;
  width: 100%;
  color: black;
  background-color: white;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 1px;
  font-size: 14px;
  line-height: 17px;
}

.form__input:focus {
  outline: 1px solid rgba(0, 0, 0, 0.2);
}
